@import url(https://fonts.googleapis.com/css?family=Roboto:200,400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Global Styles */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local('Roboto'), local('Roboto-Regular'), url(https://fonts.gstatic.com/s/roboto/v18/KFOmCnqEu92Fr1Mu4mxP.ttf) format('truetype');
}
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local('Roboto Bold'), local('Roboto-Bold'), url(https://fonts.gstatic.com/s/roboto/v18/KFOlCnqEu92Fr1MmWUlfBBc9.ttf) format('truetype');
}
::-webkit-scrollbar {
  width: 0px;
}
.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  display: block;
  transition: background-color 0.5s ease;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  overflow-x: hidden;
}
.main-section {
  position: absolute;
  height: 100vh;
  width: 100%;
  top: 1px;
}
.section {
  position: absolute;
  height: 500vh;
  width: 100%;
}
.inner-section {
  height: 100vh;
  width: 100%;
}
/* Transitions */
.bg-color-white {
  background-color: white !important;
}
.bg-color-black {
  background-color: black !important;
}
.color-white {
  color: white !important;
}
.color-black {
  color: black !important;
}
.display-none {
  opacity: 0;
}
/* Main */
.main {
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
/* Titles */
.main .info .title {
  font-size: 80px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main .info .title .framer {
  padding-right: 30px;
  font-weight: 700;
}
.main .info .title .react {
  padding-left: 30px;
  font-weight: 700;
}
.main .info .subtitle {
  font-weight: 400;
  font-size: 34px;
  margin-top: 7px;
  letter-spacing: 2px;
}
.main .info .tags {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}
.main .info .tags .tag {
  width: 140px;
  height: 22px;
  border-radius: 42px;
  background-color: #0053FF;
  font-size: 18px;
  font-weight: 700;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: 7px;
  margin-right: 7px;
}
/* Scroll */
.scroll {
  position: fixed;
  display: inline-flex;
  bottom: 69px;
}
.scroll .mouse {
  position: fixed;
  bottom: 60px;
  width: 36px;
  height: 36px;
  margin-left: auto;
  margin-right: auto;
  -webkit-transform: translateX(-15px);
          transform: translateX(-15px);
  transition: opacity 0.2s ease;
}
.scroll .mouse .mousecircle {
  position: relative;
  width: 36px;
  height: 36px;
}
@-webkit-keyframes bounce {
  from {
    bottom: 32px;
  }
  to {
    bottom: 40px;
  }
}
@keyframes bounce {
  from {
    bottom: 32px;
  }
  to {
    bottom: 40px;
  }
}
.scroll .mouse .mousescroll {
  position: relative;
  width: 36px;
  height: 36px;
  -webkit-animation: bounce 0.6s infinite alternate;
          animation: bounce 0.6s infinite alternate;
}
.scroll .scrolltext {
  margin-left: 28px;
  color: #0053FF;
  font-size: 16px;
  transition: opacity 0.2s ease;
}
/* About */
.menus .about {
  position: fixed;
  top: 60px;
  right: 60px;
  height: 40px;
  width: 100px;
  border-radius: 40px;
  color: #0053FF;
  background-color: rgba(0, 0, 0, 0);
  font-size: 18px;
  font-weight: 700;
  border-color: #0053FF;
  border-width: 2px;
  transition: background-color 0.5s ease;
}
.menus .about:hover {
  color: white;
  background-color: #0053FF !important;
  transition: 0.5s;
  cursor: pointer;
}
.menus .about:focus {
  outline: none;
  color: white;
  background-color: #0053FF !important;
}
/* Side Menu */
.menus .sidemenu {
  position: fixed;
  top: 56px;
  left: 60px;
  font-weight: 700;
  font-size: 20px;
  text-align: left;
}
.menus .sidemenu .menu-item {
  margin-bottom: 4px;
  padding-right: 60px;
  padding-top: 4px;
  padding-bottom: 4px;
  transition: color 0.5s ease;
  display: block;
}
.menus .sidemenu .hidden {
  width: 0px;
  height: 0px;
  display: block;
  background: rgba(0, 0, 0, 0);
}
.menus .sidemenu .selected {
  color: #0053FF !important;
}
.menus .sidemenu .selected::before {
  content: "\2015\2015   ";
  color: #0053FF !important;
}
.menus .sidemenu .menu-item:hover {
  cursor: pointer;
}
.menus .sidemenu .menu-item:hover::before {
  content: "\2015\2015   ";
}
/* Copyright */
.menus .copyright {
  position: fixed;
  left: 4px;
  bottom: 112px;
  color: #707070;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
/* iPhone */
.iphone {
  position: absolute;
  left: calc(40% - 180px);
  top: 0px;
  margin-top: calc(50vh - 261px);
  margin-bottom: calc(50vh - 261px);
  width: 260px;
  z-index: 100;
}
.project {
  position: absolute;
  margin-left: calc(40% - 165px);
  width: 230px;
  height: 493px;
  transition: opacity 0.1s;
}
/* Details */
.inner-section {
  align-items: center;
  display: flex;
  position: absolute;
}
.first-inner-section {
  align-items: center;
  justify-content: center;
  height: 100vh;
  top: 0px;
}
.inner-section .details {
  position: absolute;
  left: calc(55%);
  width: 360px;
  text-align: left;
}
.inner-section .details .day {
  font-weight: 700;
  font-size: 20px;
  margin: 0px;
}
.inner-section .details .title {
  font-weight: 700;
  font-size: 40px;
  margin-top: 20px;
  margin-bottom: 0px;
}
.inner-section .details .description {
  font-size: 16px;
  margin-top: 20px;
  margin-bottom: 0px;
}
.inner-section .details .logo {
  width: 40px;
  height: 40px;
  margin-top: 40px;
  margin-right: 10px;
}
.inner-section .details .logo:hover {
  cursor: pointer;
}
.inner-section .details a:focus {
  outline: none;
}
/* About Layer */
.about-layer {
  position: fixed;
  z-index: 200;
  width: calc(100% - 100px);
  height: calc(100vh - 100px);
  left: 50px;
  top: 50px;
  background-color: white;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.5s;
}
.about-layer .introduction {
  position: absolute;
  width: 50%;
  height: 100%;
  left: 0px;
  background-color: #0053FF;
  color: white;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-layer .introduction .introduction-inner {
  width: 310px;
}
.about-layer .introduction .introduction-inner .title {
  font-weight: 700;
  font-size: 28px;
  margin: 0px 0px 20px 0px;
}
.about-layer .introduction .introduction-inner .description {
  font-weight: 200;
  font-size: 28px;
  margin: 0px 0px 50px 0px;
}
.about-layer .introduction .introduction-inner .credit-pc {
  font-weight: 200;
  font-size: 14px;
  margin: 0;
}
.about-layer .introduction .introduction-inner .credit-mobile {
  display: none;
}
.about-layer .connect {
  position: absolute;
  width: 50%;
  height: 100%;
  right: 0px;
  background-color: white;
  color: #0053FF;
  text-align: left;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about-layer .connect .connect-inner {
  width: 310px;
  height: 350px;
}
.about-layer .connect .connect-inner .connect-title {
  font-weight: 700;
  font-size: 28px;
  margin: 0px 0px 86px 0px;
}
.about-layer .connect .connect-inner a {
  display: block;
  color: #0053FF;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  font-size: 28px;
  margin-bottom: 11px;
  font-weight: 700;
}
.about-layer button {
  width: 50px;
  height: 50px;
  padding: 0;
  margin: 0;
  border: 2px solid #0053FF;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
}
.about-layer button:focus {
  outline: none;
}
.about-layer .close {
  position: absolute;
  right: 30px;
  top: 30px;
  width: 50px;
  height: 50px;
  transition: background-color 0.5s ease;
}
.about-layer .close svg {
  position: absolute;
  left: -2px;
  top: -2px;
  transition: fill 0.5s ease;
  width: 50px;
  height: 50px;
}
.about-layer .close svg path {
  fill: #0053FF;
}
.about-layer .close:hover {
  cursor: pointer;
  background-color: #0053FF;
}
.about-layer .close:hover svg path {
  fill: white;
}
.show {
  visibility: visible !important;
  opacity: 100 !important;
}
.hide {
  visibility: hidden !important;
}

/* Global Styles */

@media screen and (max-width: 930px) {
  .sidemenu,
  .copyright,
  .inner-section .details .description,
  .logo {
    display: none;
  }

  .main .info {
    margin-top: -100px;
  }

  .main .info .title {
    font-size: 26px;
  }

  .main .info .title .framer {
    padding-right: 15px;
  }

  .main .info .title .cross {
    width: 20px;
    height: 20px;
  }

  .main .info .title .react {
    padding-left: 15px;
  }

  .main .info .subtitle {
    font-size: 20px;
  }

  .main .info .tags {
    justify-content: center;
    margin-top: 40px;
  }

  .main .info .tags .tag {
    width: 90px;
    height: 14px;
    font-size: 11px;
    margin-left: 3px;
    margin-right: 3px;
  }

  .scroll .scrolltext {
    font-size: 14px;
    font-weight: 400;
  }

  .menus .about {
    position: absolute;
    top: 30px;
    right: 30px;
    height: 40px;
    width: 80px;
    border-radius: 30px;
    font-size: 14px;
  }

  .about-layer {
    width: calc(100% - 50px);
    height: calc(100vh - 50px);
    left: 25px;
    top: 25px;
  }

  .about-layer .connect {
    width: 100%;
    height: 50%;
  }

  .about-layer .connect .connect-inner {
    width: 260px;
    height: 180px;
  }

  .about-layer .connect .connect-inner .connect-title {
    font-size: 20px;
    margin: 0px 0px 20px 0px;
  }

  .about-layer .connect .connect-inner a {
    font-size: 20px;
    margin-bottom: 11px;
  }

  .about-layer button {
    width: 30px;
    height: 30px;
  }

  .about-layer .close {
    right: 35px;
    top: 35px;
    width: 36px;
    height: 36px;
  }

  .about-layer .close svg {
    left: -9px;
    top: -9px;
    width: 40px;
    height: 40px;
  }

  .about-layer .introduction {
    width: 100%;
    height: 50%;
    top: calc(50vh - 25px);
  }

  .about-layer .introduction .introduction-inner {
    width: 260px;
    height: 180px;
  }

  .about-layer .introduction .introduction-inner .title {
    font-size: 20px;
    margin: 0px 0px 24px 0px;
    height: 50px;
  }

  .about-layer .introduction .introduction-inner .description {
    font-size: 14px;
    margin: 0px 0px 0px 0px;
    height: 96px;
  }

  .about-layer .introduction .introduction-inner .credit-pc {
    display: none;
  }

  .about-layer .introduction .introduction-inner .credit-mobile {
    display: inline;
    display: initial;
    font-weight: bold;
    font-size: 14px;
    margin: 0;
  }

  .iphone {
    left: calc(50% - 130px);
    margin-top: calc(50vh + 60px - 301px);
    margin-bottom: calc(50vh + 60px - 221px);
  }

  .project {
    z-index: 10;
    top: 0;
    margin-left: calc(50% - 114px);
    margin-top: calc(50vh + 60px - 286px);
    margin-bottom: calc(50vh + 60px - 226px);
  }

  .inner-section .details {
    left: 0px;
    top: 0px;
    width: 100%;
    justify-content: center;
    text-align: center;
  }

  .inner-section .details .day {
    padding-left: auto;
    padding-right: auto;
    font-size: 16px;
  }

  .inner-section .details .title {
    font-size: 20px;
    margin-top: 5px;
  }
}

@media screen and (min-width: 931px) {
  .about-layer .connect .connect-inner a:focus {
    outline: none;
  }

  .about-layer .connect .connect-inner a:focus::before {
    content: "\2015\2015   ";
    color: #0053FF;
    outline: none;
  }

  .about-layer .connect .connect-inner a:hover {
    cursor: pointer;
  }

  .about-layer .connect .connect-inner a:hover::before {
    content: "\2015\2015   ";
    color: #0053FF;
  }
}

